* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

.tasks__card {
  width: 400px;
  height: 60px;
  display: flex;
  align-items: center;
  background: #38404b;
  box-shadow: -5px 5px 10px rgba(48, 54, 64, 0.2),
    5px -5px 10px rgba(48, 54, 64, 0.2), -5px -5px 10px rgba(64, 74, 86, 0.9),
    5px 5px 13px rgba(48, 54, 64, 0.9), inset 1px 1px 2px rgba(64, 74, 86, 0.3),
    inset -1px -1px 2px rgba(48, 54, 64, 0.5);
  border-radius: 16px;
  padding: 10px 15px;
  margin: 30px 30px;
}

.card__img {
  width: 10%;
  cursor: pointer;
}

.card__content {
  width: 80%;
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #94adcf;
}

.card__close {
  width: 10%;
}

.close__img {
  transform: rotate(45deg);
  cursor: pointer;
}

@media (max-width: 430px) {
  .container {
    max-width: 430px;
    margin: 0 auto;
  }

  .tasks__card {
    width: 300px;
  }
}
